.card-header {
   position: relative;
   display: flex;
   height: 200px;
   flex-shrink: 0;
   width: 100%;
   transition: 0.3s;

   * {
      transition: 0.3s;
   }
}

.card-cover {
   width: 100%;
   height: 100%;
   position: absolute;
   height: 160px;
   top: -20%;
   left: 0;
   will-change: top;
   background-size: cover;
   background-position: center;
   filter: blur(30px);
   transform: scale(1.2);
   transition: 0.5s;
}

.card-avatar {
   width: 100px;
   height: 100px;
   box-shadow: 0 8px 8px rgba(0, 0, 0, 0.2);
   border-radius: 50%;
   object-position: center;
   object-fit: cover;
   position: absolute;
   bottom: 0;
   left: 50%;
   transform: translateX(-50%) translateY(-64px);
}

.card-fullname {
   position: absolute;
   bottom: 0;
   font-size: 22px;
   font-weight: 700;
   text-align: center;
   white-space: nowrap;
   transform: translateY(-10px) translateX(-50%);
   left: 50%;
}

.card-jobtitle {
   position: absolute;
   bottom: 0;
   left: 50%;
   transform: translateX(-50%) translateY(-7px);
   font-size: 11px;
   white-space: nowrap;
   font-weight: 500;
   opacity: 0.7;
   text-transform: uppercase;
   letter-spacing: 1.5px;
   margin: 0 0 -10px 0 ;
}
